<!-- ALL THE HTML -->
<template>
  <div id="app" style="padding-top: 20px;padding-bottom: 20px">
    <div
        v-for="(item, index) in clouds"
        class="cloud"
        :id="'cloud-' + index"
        :style="makeStarStyles(index)"
        :key="index"
    ></div>
    <!-- <button class="testbtn" @click="changeDisplay">Fire!</button> -->

    <div id="holoLoader" :class="displayMode ? 'active' : 'notActive'">
      <nav class="menu">

        <ul>
          <li class="menu-item">
            <a ref="#roadmap"> Roadmap <p> R̷o̸a̸d̵m̶a̶p̸</p></a>

          </li>
          <li class="menu-item">
            <a href="#testimonial">Testimonial <p>T̶e̸s̵t̵i̷o̴m̷o̶n̵i̴a̸l̸</p></a>

          </li>
          <li class="menu-item">
            <a href="#home">Home<p>H̶o̸m̵e̸ </p></a>

          </li>
          <li class="menu-item">
            <a href="#utility">Utility<p>U̵t̷i̴l̶i̴t̴y̶</p></a>

          </li>
          <li class="menu-item">
            <a href="#ourchannels">Our Channels <p>O̴u̸r̸ ̴C̴h̵a̸n̷n̶e̸l̷s̶</p></a>

          </li>
          <li class="menu-item">
            <a href="#gallery">Gallery <p>G̵a̵l̸l̵e̴r̷y̴</p></a>
          </li>
        </ul>
      </nav>
      <div class="holoLayer" id="core">
        <canvas id="coreCanvas" style="border-color: transparent;border-style: solid;border-width: 2px;"/>
      </div>
      <div class="holoLayer" id="firstCircle">
        <canvas width="450" height="450" id="secondCanvas"></canvas>
      </div>
      <div class="holoLayer" id="secondCircle">
        <div class="slider a">
          <div class="sliderPoint a">
            <!--<div :key="item.toString()" v-for="item in sparks" class="spark" />-->
          </div>
        </div>
      </div>
      <div class="holoLayer" id="thirdCircle"></div>
      <div class="holoLayer" id="fourthCircle"></div>
      <div class="holoLayer" id="fifthCircle">
        <canvas id="firstCanvas"/>
        <canvas id="textCanvas"/>
      </div>
      <div class="holoLayer" id="sixthCircle"></div>
    </div>
    <canvas
        width="470"
        height="200"
        id="shootingParticles"
        :class="displayMode ? 'active' : 'notActive'"
    />
  </div>
</template>
<!-- ALL THE JAVASRIPT -->
<script>
export default {
  data() {
    return {
      clouds: 0,
      numberOfCoreParticles: 15,
      particles: [],
      numberOfShootingParticles: 5,
      shootingParticles: [],
      ringNumber: 5,
      stylesArray: [],
      OuterRings: [],
      instances: 40,
      numberOfExternalRingSegments: 70,
      externalRingSegments: [],
      loadingAngleStart: 7.5,
      then: Date.now(),
      displayModeEngage: Date.now(),
      displayMode: false,
      angles: [
        [0, 25, 5, "rgba(142,1,61,255)"],
        [50, 76, 10, "rgba(142,1,61,255)"],
        [101, 126, 5, "rgba(142,1,61,255)"],
        [152, 177, 10, "rgba(142,10,61,255)"],
        [203, 227, 5, "rgba(142,1,61,255)"],
        [258, 279, 10, "rgba(142,1,61,255)"],
        [305, 334, 5, "rgba(142,1,61,255)"],
      ],
    };
  },
  methods: {
    changeDisplay() {
      //this.displayMode = !this.displayMode
      this.displayModeEngage = Date.now();
      //console.log("changed display!")
    },
    loop() {

      this.update();
      this.render();
      requestAnimationFrame(this.loop);
    },
    update() {
      if (this.displayMode === false) {
        this.loadingAngleStart = this.loadingAngleStart + 0.2;
        this.loadingAngleStart >= 100 ? (this.changeDisplay(), (this.loadingAngleStart = 100)) : null;
      } else {
        this.loadingAngleStart = this.loadingAngleStart - 0.4;
        this.loadingAngleStart <= 0 ? (this.changeDisplay(), (this.loadingAngleStart = 0)) : null;
      }

      // eslint-disable-next-line no-unused-vars
      this.particles.forEach((item, index) => {
        this.adjustLength(item);
        this.moveParticle(item);
      });
      // eslint-disable-next-line no-unused-vars
      this.shootingParticles.forEach((item, index) => {
        this.moveShootingParticle(item);
      });
      // eslint-disable-next-line no-unused-vars
      this.OuterRings.forEach((item, index) => {
        this.updateRings(item);
      });
      // eslint-disable-next-line no-unused-vars
      this.externalRingSegments.forEach((item, index) => {
        this.updateCount(item);
      });
      //console.log("we updating!")
    },
    render() {
      let coreCanvas = document.getElementById("coreCanvas");
      let firstCanvas = document.getElementById("firstCanvas");
      let secondCanvas = document.getElementById("secondCanvas");
      let shootingParticles = document.getElementById("shootingParticles");
      let coreCanvasContext = coreCanvas.getContext("2d");
      let firstCanvasContext = firstCanvas.getContext("2d");
      let secondCanvasContext = secondCanvas.getContext("2d");
      let shootingParticlesContext = shootingParticles.getContext("2d");
      coreCanvasContext.globalCompositeOperation = "screen"; //test */
      shootingParticlesContext.globalCompositeOperation = "screen"; //test */
      coreCanvasContext.clearRect(0, 0, coreCanvas.width, coreCanvas.height);
      // eslint-disable-next-line no-unused-vars
      this.particles.forEach((item, index) => {
        this.renderParticle(item, coreCanvasContext);
      });
      firstCanvasContext.clearRect(0, 0, firstCanvas.width, firstCanvas.height);
      this.drawCircularPlate(this.angles, firstCanvasContext);
      secondCanvasContext.clearRect(0, 0, secondCanvas.width, secondCanvas.height);
      // eslint-disable-next-line no-unused-vars
      this.externalRingSegments.forEach((item, index) => {
        this.drawCounter(item, secondCanvasContext);
      });
      this.drawShootTime(this.loadingAngleStart, secondCanvasContext);
      this.drawOuterRing(this.OuterRings, secondCanvasContext);
      shootingParticlesContext.clearRect(0, 0, shootingParticles.width, shootingParticles.height);
      // eslint-disable-next-line no-unused-vars
      this.shootingParticles.forEach((item, index) => {
        this.renderShootingParticles(item, shootingParticlesContext);
      });
    },
    createShootingParticles(number) {
      for (let n = 0; n < number; n++) {
        let obj = {
          x: 470,
          y: 95 + (Math.random() * 12 - 6),
          radius: Math.random() * 5 + 15,
          speed: Math.random() * 7 + 1,
          opacity: 1,
          delay: Math.floor(Math.random() * 1000),
        };
        this.shootingParticles.push(obj);
      }
    },
    drawCounterInitiation(number) {
      for (let n = 0; n < number; n++) {
        let obj = {
          number: n,
          opacity: Math.random(),
          reverse: true,
          start: n * (360 / number),
          finish: (n + 1) * (360 / number),
          fill: "transparent",
          shadow: "rgba(255,255,255,0.5)",
          strokeStyle: "116,4,224",
          lineWidth: Math.floor(Math.random() * 5 + 3),
        };
        this.externalRingSegments.push(obj);
      }
    },
    drawShootTime(start, context) {
      let finish = (360 / 100) * start - 90;
      context.strokeStyle = `rgba(175, 7, 82, ${start / 100})`;
      context.beginPath();
      context.arc(
          450 / 2,
          450 / 2,
          185,
          -90 * (Math.PI / 180),
          finish * (Math.PI / 180)
      );
      context.stroke();
      context.closePath();
      // X = Math.cos((angle * Math.PI) / 180) * length + startPointX;
      let calculateX = Math.cos(((finish + 5) * Math.PI) / 180) * 185 + 450 / 2;
      //Y = Math.sin(angle*Math.PI/180) * length + startPointY
      let calculateY = Math.sin(((finish + 5) * Math.PI) / 180) * 185 + 450 / 2;
      context.fillStyle = "white";
      context.textAlign = "center";
      context.font = "10px Arial";
      context.fillText(
          start.toString().substring(0, 2) + "%",
          calculateX,
          calculateY
      );
    },
    updateCount(item) {
      if (item.reverse === true) {
        item.opacity -= 0.01;
        item.opacity < 0.1 ? (item.reverse = false) : null;
      }
      if (item.reverse === false) {
        item.opacity += 0.01;
        item.opacity > 0.9 ? (item.reverse = true) : null;
      }
    },
    drawCounter(item, canvas) {
      if (item.number % 2) {
        canvas.beginPath();
        canvas.strokeStyle = "rgba(" + item.strokeStyle + "," + item.opacity + ")";
        canvas.arc(
            450 / 2,
            450 / 2,
            215 + item.lineWidth / 2,
            item.start * (Math.PI / 180),
            item.finish * (Math.PI / 180)
        );
        canvas.fillStyle = item.fill;
        canvas.fill();
        canvas.shadowColor = item.shadow;
        canvas.shadowBlur = 8;
        canvas.lineWidth = item.lineWidth;
        canvas.strokeStyle = 1;
        canvas.stroke();
        canvas.closePath();
      }
    },
    createCoreParticles(number) {
      for (let n = 0; n < number; n++) {
        let angle = Math.floor(Math.random() * 360);
        let ring = Math.floor(Math.random() * 25);
        let length = 15 + Math.random() * 35;
        let speed = Math.random() - 1.5;
        // eslint-disable-next-line no-self-assign
        if (speed > 0.3) {
          (speed = 0.3)
        }
        let particle = {
          x: -40 + (length + ring * Math.cos(angle * (Math.PI / 180))),
          y: -40 + (length + ring * Math.sin(angle * (Math.PI / 180))),
          radius: Math.random() * 2 + 3,
          startRadius: Math.random() * 2 + 12,
          length: length,
          speed: speed,
          usedLength: length,
          grow: Math.random() > 0.5,
          startAngle: angle,
          number: n,
          usedAngle: angle,
          ring: ring,
          color:
              Math.random() > 0.5 ? "rgba(255,255,255,1)" : "rgba(214,107,24,0.3)",
          shadow: "rgba(255, 255, 255,1)",
        };
        this.particles.push(particle);
      }
    },
    makeStarStyles(cloudIndex) {
      if (document.getElementById("cloud-" + cloudIndex) === null) {
        let radius = Math.floor(Math.random() * 230 + 130);
        let x =
            "top:" +
            Math.floor(Math.random() * document.body.clientHeight - radius / 2) +
            "px";
        let y =
            "left:" +
            Math.floor(Math.random() * document.body.clientWidth - radius / 2) +
            "px";
        let animationTime = Math.floor(Math.random() * 10 + 5);
        let animationDelay = Math.floor(Math.random() * 15);
        let background =
            "background:radial-gradient(circle, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 34%, rgba(217,217,217,0.0) 70%)";
        let style =
            "position:absolute;" +
            x +
            ";" +
            y +
            ";" +
            "height:" +
            radius +
            "px;" +
            "width:" +
            radius +
            "px;" +
            "border-radius:180px;" +
            background +
            ";animation-duration: " +
            animationTime +
            "s;animation-delay:" +
            animationDelay +
            "s;";
        this.stylesArray.push(style);
        //console.log(style)
        return style;
      } else {
        return this.stylesArray[cloudIndex];
      }
    },
    renderParticle(particle, canvas) {
      var grd = canvas.createRadialGradient(
          particle.x,
          particle.y,
          1,
          particle.x,
          particle.y,
          particle.radius - particle.radius / 10
      );
      grd.addColorStop(0, particle.color);
      grd.addColorStop(1, "rgba(255,255,255,0)");
      canvas.fillStyle = grd;
      canvas.beginPath();
      canvas.arc(particle.x, particle.y, particle.radius, 0, 2 * Math.PI);
      canvas.closePath();
      canvas.fill();
    },
    renderShootingParticles(particle, canvas) {
      if (particle.x < 470) {
        canvas.fillStyle = "rgba(255,255,255," + particle.opacity + ")";
        canvas.beginPath();
        canvas.arc(particle.x, particle.y, particle.radius, 0, 2 * Math.PI);
        canvas.closePath();
        canvas.fill();
      }
    },
    moveShootingParticle(item) {
      if (
          this.displayMode === true &&
          Date.now() > this.displayModeEngage + 1000
      ) {
        item.opacity = item.x / 470;
        (item.x / 470) * 10 > 0
            ? (item.radius = (item.x / 470) * 10)
            : (item.radius = 0);
        if (item.x > 0) {
          if (Date.now() > this.displayModeEngage + 1000 + item.delay) {
            item.x = item.x - item.speed;
          }
        } else {
          item.x = 470;
        }
      } else if (
          this.displayMode === false &&
          Date.now() < this.displayModeEngage + 1500
      ) {
        item.opacity = item.x / 470;
        (item.x / 470) * 10 > 0
            ? (item.radius = (item.x / 470) * 10)
            : (item.radius = 0);
        item.x = item.x - item.speed;
      } else if (
          this.displayMode === false &&
          Date.now() > this.displayModeEngage + 1500
      ) {
        item.x = 470;
      }
    },
    createRings(rings, array) {
      //console.logs
      for (let i = 0; i < rings; i++) {

        let startAng = Math.floor(Math.random() * Math.random() * 420);
        let endAng = startAng + Math.random() * 80 + Math.random() * 50;
        //let distance = Math.floor(Math.random() * 50 + 50)

        let obj = {
          startAng: startAng * (Math.PI / 180),
          endAng: endAng * (Math.PI / 180),
          length: 120 + 12 * i,
          shown: true,
          moveDirection: Math.random() > 0.5,
          moveDone: false,
        };
        array.push(obj);
        console.log(obj);
      }
    },
    updateRings(ring) {
      if (ring.moveDone === false) {
        ring.moveDirection
            ? ((ring.startAng -= 0.01), (ring.endAng -= 0.01))
            : ((ring.startAng += 0.01), (ring.endAng += 0.01));
        Math.random() < 0.02 ? (ring.moveDone = true) : null;
      } else {
        ring.moveDirection = !ring.moveDirection;
        ring.moveDone = false;
      }
    },
    drawOuterRing(points, canvas) {
      //multiples anillos (arcos) concentricos
      points.forEach((item) => {
        if (item.shown === true) {
          //console.log(item.startAng, item.endAng)
          canvas.beginPath();
          canvas.arc(
              450 / 2,
              450 / 2,
              item.length + 5,
              item.startAng,
              item.endAng,
              0,
              2 * Math.PI
          );
          canvas.fillStyle = "transparent";
          canvas.fill();
          canvas.shadowColor = "rgba(175,7,82,1)";
          canvas.shadowBlur = 15;
          canvas.lineWidth = 6;
          canvas.strokeStyle = "rgba(116,4,224,0.8)";
          canvas.stroke();
          canvas.closePath();
        }
      });
    },
    adjustLength(particle) {
      //particle.number === 0 ? console.log(particle.grow) : null
      if (particle.grow && !this.displayMode) {
        particle.usedLength += Math.abs(particle.speed) / 2;
        particle.usedLength > particle.length + 20
            ? (particle.grow = false)
            : null;
        particle.radius > particle.startRadius
            ? ((particle.radius -= 0.5),
                (particle.usedLength += particle.radius / 3))
            : null;
      } else if (!particle.grow && !this.displayMode) {
        particle.usedLength -= Math.abs(particle.speed) / 2;
        particle.usedLength < particle.length - 20
            ? (particle.grow = true)
            : null;
        particle.radius > particle.startRadius ? (particle.radius -= 1) : null;
      } else if (this.displayMode) {
        !particle.grow ? (particle.grow = true) : null;
        particle.usedLength > 0
            ? (particle.usedLength -= particle.radius / 10)
            : null;
        particle.radius < 25 ? (particle.radius += 1) : null;
      }
    },
    moveParticle(particle) {
      // Math.random() < 0.5 ? particle.usedLength++ : particle.usedLength--;
      particle.usedAngle = particle.usedAngle + particle.speed;
      particle.x =
          255 / 2 +
          particle.usedLength * Math.cos(particle.usedAngle * (Math.PI / 180));
      particle.y =
          240 / 2 +
          particle.usedLength * Math.sin(particle.usedAngle * (Math.PI / 180));
    },
    drawCircularPlate(angles, canvas) {
      // platos concentricos con segmentos diferente grosor
      angles.forEach((item) => {
        canvas.beginPath();
        canvas.arc(
            255 / 2,
            240 / 2,
            100,
            item[0] * (Math.PI / 180),
            item[1] * (Math.PI / 180)
        );
        canvas.fillStyle = "transparent";
        canvas.fill();
        canvas.shadowColor = item[3];
        canvas.shadowBlur = 15;
        canvas.lineWidth = item[2];
        canvas.strokeStyle = item[3];
        canvas.stroke();
        canvas.closePath();
      });
      canvas.beginPath();
      canvas.arc(255 / 2, 240 / 2, 110, 0, 2 * Math.PI);
      canvas.fillStyle = "transparent";
      canvas.fill();
      canvas.shadowColor = "rgba(142,1,61,255)";
      canvas.shadowBlur = 15;
      canvas.lineWidth = 2;
      canvas.strokeStyle = "rgba(175,7,82,1)";
      canvas.stroke();
      canvas.closePath();

      canvas.beginPath();
      canvas.arc(255 / 2, 240 / 2, 90, 0, 2 * Math.PI);
      canvas.fillStyle = "transparent";
      canvas.fill();
      canvas.shadowColor = "rgba(142,1,61,255)";
      canvas.shadowBlur = 15;
      canvas.lineWidth = 2;
      canvas.strokeStyle = "rgba(175,7,82,1)";
      canvas.stroke();
      canvas.closePath();
    },
    drawText(context, text) {
      context.font = '48px serif';
      context.fillText(text, 10, 50);
    }
  },
  mounted() {
    this.drawCounterInitiation(this.numberOfExternalRingSegments);
    this.createCoreParticles(this.numberOfCoreParticles);
    // this.createShootingParticles(this.numberOfShootingParticles);
    this.createRings(this.ringNumber, this.OuterRings);
    let coreCanvas = document.getElementById("coreCanvas");
    coreCanvas.width = document.getElementById("core").offsetWidth;
    coreCanvas.height = document.getElementById("core").offsetHeight;
    let firstCanvas = document.getElementById("firstCanvas");
    firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
    firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
    let firstCanvasContext = firstCanvas.getContext("2d");
    this.drawCircularPlate(this.angles, firstCanvasContext);
    // this.drawText(firstCanvas,"hola mundos")
    firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
    firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
    window.addEventListener(
        "resize",
        function () {
          let coreCanvas = document.getElementById("coreCanvas");
          coreCanvas.width = document.getElementById("core").offsetWidth;
          coreCanvas.height = document.getElementById("core").offsetHeight;

          let firstCanvas = document.getElementById("firstCanvas");
          firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
          firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
          firstCanvas.width = document.getElementById("firstCircle").offsetWidth;
          firstCanvas.height = document.getElementById("firstCircle").offsetHeight;
        },
        true
    );
    //Let's loadingAngleStart this!
    //we go!
    this.loop();
  },
};
</script>
<!-- ALL THE STYLES -->
<style>
:root {
  --main-blue-color: #13aeff;
  --main-white-color: #7be326;
  --main-orange-color: #0328d9;
}

#app {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(4, 5, 21);
}


.cloud {
  z-index: -1;
  opacity: 0.5;
  animation: cloudFade;
}

@keyframes cloudFade {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes reverseRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

#secondCircle .slider {
  background-color: transparent;
  width: calc(50% + 4px);
  box-sizing: border-box;
  height: 8px;
  top: calc(50% - 4px);
  left: calc(50% - 4px);
  position: relative;
  animation: rotate 12s infinite linear;
  transform-origin: 4px 4px;
}

#secondCircle .sliderPoint.a {
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  left: calc(100% - 2px);
  width: 4px;
  background-color: rgba(255, 255, 255, 0.55);
  position: relative;
  box-sizing: border-box;
  border-radius: 100%;
  height: 4px;
}

#secondCircle .sliderPoint.a:before {
  content: "";
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  width: 4px;
  position: relative;
  top: 105px;
  left: -45px;
  border-radius: 100%;
  height: 4px;
  display: block;
  background-color: rgba(255, 255, 255, 0.55);
}

#secondCircle .sliderPoint.a:after {
  content: "";
  box-shadow: 0 0 3px 2px rgba(255, 255, 255, 0.55);
  width: 4px;
  position: relative;
  top: -85px;
  left: -25px;
  border-radius: 90px;
  height: 4px;
  display: block;
  background-color: rgba(255, 255, 255, 0.55);
}

#holoLoader.active .sliderPoint.a:after {
  animation: sparks 3s infinite linear 0s;
}

#holoLoader.active .sliderPoint.a:before {
  animation: sparks 3s infinite linear 1.2s;
}

#holoLoader.active .sliderPoint.a {
  animation: sparks 3s infinite linear 0.3s;
}

@keyframes sparks {
  0% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  60% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  61% {
    box-shadow: 0 0 5px 8px rgba(255, 255, 255, 0.75);
  }
  62% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  84% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
  85% {
    box-shadow: 0 0 5px 8px rgba(255, 255, 255, 0.75);
  }
  86% {
    box-shadow: 0 0 5px 3px rgba(255, 255, 255, 0.75);
  }
}

#holoLoader {
  z-index: 1;
  position: relative;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  width: 275px;
  height: 275px;
  margin: auto;

  transition: all 0.5s ease-in-out 0.4s;
  /*transform: perspective(1000px) rotateX(30deg) rotateY(-50deg) rotateZ(0deg);*/
}

.holoLayer {
  /*background: radial-gradient(circle, rgba(16, 29, 216, 0.219) 0%, rgba(1, 4, 187, 0.103) 65%, rgba(147,196,255,1) 100%);*/
  /*box-shadow: 0 0 39px 0 rgba(3, 40, 217, 0.3);*/
  border-radius: 360px;
  transition: all 0.5s ease-in-out 0s;
  z-index: -999999;
}

#holoLoader.active {
  transform: perspective(1000px) rotateX(30deg) rotateY(-50deg) rotateZ(120deg);
  transition: all 0.5s ease-in-out;
}

#core {
  width: 100%;
  height: 100%;
  background: radial-gradient(
      circle,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0) 17%,
      rgba(147, 196, 255, 0.1) 24%,
      rgba(255, 255, 255, 0) 30%
  );
}

#coreCanvas,
#firstCanvas,
#secondCanvas {
  width: 100%;
  height: 100%;
}

#firstCircle {
  position: absolute;
  top: 0;
  width: 90%;
  height: 90%;
  margin: 5%;
}

@keyframes stutterMovement {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(50deg);
  }
  30% {
    transform: rotate(50deg);
  }
  40% {
    transform: rotate(50deg);
  }
  45% {
    transform: rotate(70deg);
  }
  60% {
    transform: rotate(-20deg);
  }
  70% {
    transform: rotate(0deg);
  }
  85% {
    transform: rotate(20deg);
  }
  90% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(45deg);
  }
}

#holoLoader.active #firstCircle {
  transform: translateX(-130px) translateY(-80px);
  transition: all 0.5s ease-in-out 0.4s;
}

#secondCircle {
  position: absolute;
  top: 0;
  width: 80%;
  height: 80%;
  margin: 10%;
  border: 1px solid var(--main-orange-color);
}

#secondCircle:before {
  transition: all 0.5s ease-in-out 0s;
  content: "";
  border: 7px dotted rgba(79, 223, 255, 0.3);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
  border-radius: 360%;
  opacity: 0;
}

#secondCircle:after {
  transition: all 0.5s ease-in-out 0s;
  content: "";
  border: 5px solid rgba(79, 223, 255, 0.7);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 360%;
  opacity: 0;
}

#holoLoader.active #secondCircle {
  border: 2px solid rgba(175, 7, 82, 0.5);
  transform: translateX(-180px) translateY(-120px);
  transition: all 0.5s ease-in-out 0.4s;
}

#holoLoader.active #secondCircle:before {
  opacity: 1;
  top: -55px;
  left: -75px;
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotation 5s linear 0s infinite;
}

@keyframes rotation {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}

#holoLoader.active #secondCircle:after {
  opacity: 1;
  transform: translateX(45px) translateY(35px);
  transition: all 0.5s ease-in-out 0.4s;
}

#thirdCircle {
  position: absolute;
  top: 0;
  width: 70%;
  height: 70%;
  margin: 15%;
  border: 0 solid white;
  opacity: 0;
  transition: all 0.5s ease-in-out 0s;
}

#holoLoader.active #thirdCircle:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
}

#holoLoader.active #thirdCircle:after {
  content: "";
  position: absolute;
  display: block;
  border-radius: 180%;
  width: 100%;
  height: 100%;
}

#holoLoader.active #thirdCircle {
  border: 5px solid white;
  opacity: 1;
  transform: translateX(-520px) translateY(-330px);
  transition: all 0.5s ease-in-out 0.4s;
}

#fourthCircle {
  position: absolute;
  top: 0;
  width: 60%;
  height: 60%;
  margin: 20%;
}

#holoLoader.active #fourthCircle {
  transform: translateX(-810px) translateY(-540px);
  transition: all 0.5s ease-in-out 0.4s;
}

#fifthCircle {
  position: absolute;
  top: 0;
  width: 50%;
  height: 50%;
  margin: 25%;
  animation: stutterMovement 5s infinite linear;
}

#holoLoader.active #fifthCircle {
  transform: translateX(-600px) translateY(-400px);
  transition: all 0.5s ease-in-out 0.4s;
}

#sixthCircle {
  position: absolute;
  top: 0;
  width: 40%;
  height: 40%;
  margin: 30%;
  border: 1px dashed var(--main-orange-color);
  border-radius: 180%;
}

#holoLoader.active #sixthCircle {
  border: 3px solid var(--main-white-color);
  transition: all 0.5s ease-in-out 0.4s;
}

#holoLoader.active #sixthCircle:before {
  position: absolute;
  width: 40%;
  height: 40%;
  margin: 30%;
  top: -5px;
  left: -5px;
  border: 1px dashed var(--main-white-color);
  border-radius: 180%;
  transition: all 0.5s ease-in-out 0s;
  animation: bubbleEffect 4s infinite linear;
  box-shadow: 0 0 0 rgba(255, 255, 255, 0);
}

#sixthCircle:before {
  content: "";
  position: absolute;
  border-radius: 180%;
  width: 20%;
  display: block;
  height: 20%;
  margin: 40%;
  top: -5px;
  left: -5px;
  border: 1px solid var(--main-white-color);
  box-shadow: 0 0 5px 2px rgba(142, 1, 61, 255),
  inset 0 0 5px rgb(142, 1, 61);
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotationY 4s infinite linear;
}

#holoLoader.active #sixthCircle:after {
  position: absolute;
  width: 40%;
  height: 40%;
  margin: 30%;
  top: -5px;
  left: -5px;
  border: 1px dashed var(--main-white-color);
  border-radius: 180%;
  transition: all 0.5s ease-in-out 0s;
  animation: bubbleEffect 4s infinite linear;
  box-shadow: 0 0 0 rgba(141, 42, 42, 0);
}

#sixthCircle:after {
  content: "";
  position: absolute;
  display: block;
  width: 20%;
  height: 20%;
  border-radius: 180%;
  margin: 40%;
  top: -5px;
  left: -5px;
  box-shadow: 0 0 5px 2px var(--main-orange-color), inset 0 0 5px var(--main-orange-color);
  border: 1px solid var(--main-white-color);
  transition: all 0.5s ease-in-out 0.4s;
  animation: rotationX 8s infinite linear;
}

@keyframes rotationX {
  0% {
    transform: rotateX(0deg) rotateY(0deg) scale(1);
  }
  100% {
    transform: rotateX(360deg) rotateY(-360deg) scale(1);
  }
}

@keyframes rotationY {
  0% {
    transform: rotateY(0deg) rotateX(0deg) scale(1);
  }
  100% {
    transform: rotateY(360deg) rotateX(360deg) scale(1);
  }
}

@keyframes bubbleEffect {
  0% {
    animation-timing-function: linear;
    transform: scale(1);
  }
  25% {
    animation-timing-function: linear;
    opacity: 1;
    transform: scale(1.2);
  }
  50% {
    animation-timing-function: linear;
    opacity: 0.5;
    transform: scale(1);
  }
  75% {
    animation-timing-function: linear;
    opacity: 0.5;
    transform: scale(0.8);
  }
  100% {
    animation-timing-function: linear;
    opacity: 1;
    transform: scale(1);
  }
}

#shootingParticles {
  opacity: 0;
  width: 270px;
  height: 100px;
  z-index: 1;
  position: absolute;
  top: calc(50vh - 168px);
  left: calc(50vw - 460px);
  transform: rotateZ(18deg);
  transition: all 0.5s linear 0.5s;
}

#shootingParticles.active {
  transition: all 0.5s linear 1s;
  opacity: 1;
}


